import MenuItem from "./MenuItem";
import logo from "assets/img/logo.png";
import MobileMenu from "./MobileMenu";
import Hamburger from "hamburger-react";
import classes from "./layout.module.scss";
import { useNavigate } from "react-router";

const Header = ({
  isActive,
  handleMenuItemClick,
  open,
  setOpen,
  menuRoutes,
}) => {
  const navigate = useNavigate();

  const navigateHomePage = () => {
    navigate("/home");
  };
  return (
    <header>
      <div className={`container ${classes.mainHeaderContent}`}>
        <div className={`${classes.logoBlock} logoBackSide`}>
          <img src={logo} alt="logo" onClick={navigateHomePage} />
        </div>
        <div className={`${classes.menuLinks} ${classes.desktopMenu}`}>
          {menuRoutes().map((route) => (
            <MenuItem
              title={route?.title}
              disabled={route?.disabled}
              onClick={() => handleMenuItemClick({ route: route })}
              active={isActive(route)}
              key={route?.id}
            />
          ))}
        </div>
        <MobileMenu
          open={open}
          setOpen={setOpen}
          handleMenuItemClick={handleMenuItemClick}
          isActive={isActive}
        />
        <Hamburger
          toggle={setOpen}
          toggled={open}
          rounded
          duration={0.4}
          color={open ? "#E7F2FD" : "#011521"}
          easing="ease-in"
        />
      </div>
    </header>
  );
};

export default Header;
