import React from "react";
import MenuItem from "./MenuItem";
import { menuRoutes } from "./arrays";
import classes from "./layout.module.scss";

const MobileMenu = ({ open, handleMenuItemClick, isActive }) => {
  return (
    <div
      className={`${classes.menuLinks} rowGap-20 ${classes.mobileMenu} ${
        open ? classes.open : ""
      }`}
    >
      {menuRoutes().map((route) => (
        <MenuItem
          title={route?.title}
          disabled={route?.disabled}
          onClick={() => handleMenuItemClick({ route: route, mobile: true })}
          active={isActive(route)}
          key={route?.id}
        />
      ))}
    </div>
  );
};

export default MobileMenu;
