import { lazy, Suspense } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { publicPaths } from "config/routes";
import { observer } from "mobx-react-lite";
import PublicRoute from "core/PublicRoute";
import Loading from "components/Loading";

const HomePage = lazy(() => import("pages/Home"));
const AboutUsPage = lazy(() => import("pages/AboutUs"));
const ServicesPage = lazy(() => import("pages/Services"));
const ContactUsPage = lazy(() => import("pages/ContactUs"));

const publicRoutes = [
  { path: publicPaths.home, Component: <HomePage /> },
  { path: publicPaths.about_us, Component: <AboutUsPage /> },
  { path: publicPaths.services, Component: <ServicesPage /> },
  { path: publicPaths.contact_us, Component: <ContactUsPage /> },
];

const App = () => {
  return (
    <Suspense fallback={<Loading />}>
      <Routes>
        {publicRoutes.map((route) => (
          <Route
            key={route.path}
            path={route.path}
            element={<PublicRoute>{route.Component}</PublicRoute>}
          />
        ))}
        <Route path="*" element={<Navigate to={publicPaths.home} replace />} />
        <Route
          exact
          path="/"
          element={<Navigate to={publicPaths.home} replace />}
        />
      </Routes>
    </Suspense>
  );
};

export default observer(App);
